<template>
    <div id="app">
        <div>
            <Top
                class="search"
                :class="labelStyle"
                ref="top"
                :search="search"
            />
            <Label
                :labelStyle="labelStyle"
                :search="search"
                :fastSearchLabels="fastSearchLabels"
            />
            <VideoFrame :videoArray="videoArrayShow" />
        </div>
    </div>
</template>

<script>
import VideoFrame from "@/components/VideoFrame";
import Top from "@/components/Top";
import Label from "@/components/Label";
export default {
    name: "App",
    data() {
        return {
            videoArray: [],
            searchKey: "",
            videoArrayOther: [],
            fastSearchLabels: [
                "璃月雅集",
                "蒙德茶会",
                "特别节目",
                "版本PV",
                "角色演示",
                "角色PV",
                "剧情PV",
                "拾枝杂谈",
                "EP",
                "幕后",
                "其他",
            ],
            screenWidth: document.body.clientWidth,
            labelStyle: "right",
        };
    },
    components: { VideoFrame, Top, Label },
    computed: {
        videoArrayShow() {
            if (this.searchKey == "其他") return this.videoArrayOther;
            else
                return this.videoArray.filter(
                    (v) => v.title.indexOf(this.searchKey) >= 0
                );
        },
    },
    methods: {
        search(value) {
            this.searchKey = value;
            this.$refs["top"].$refs["userinput"].value = value;
            this.$refs["top"].$refs["userinput"].focus();
        },
        enterKey(event) {
            if (event.key == "Enter")
                this.$refs["top"].$refs["userinput"].focus();
        },
        enterKeyupDestroyed() {
            document.removeEventListener("keyup", this.enterKey);
        },
        enterKeyup() {
            document.addEventListener("keyup", this.enterKey);
        },
    },
    mounted() {
        // 绑定enter事件
        this.enterKeyup();
        // 绑定屏幕宽度事件
        window.onresize = () => {
            return (() => {
                window.screenWidth = document.body.clientWidth;
                this.screenWidth = window.screenWidth;
            })();
        };

        // 读取数据
        this.videoArray = require("@/assets/videodata.json");

        // 计算其他标签的视频
        this.videoArrayOther = this.videoArray.filter((v) => {
            var t = v.title;
            if (!t) return false;
            var flag = true;
            this.fastSearchLabels.forEach((label) => {
                if (label != "其他")
                    if (flag)
                        if (eval(`t.search(/${label}/i)`) >= 0) flag = false;
            });
            return flag;
        });
    },
    watch: {
        /* 监听*/
        screenWidth: {
            immediate: true,
            handler(val) {
                this.screenWidth = val;
                console.log("this.screenWidth", this.screenWidth);
                if (val <= 1100) this.labelStyle = "top";
                else this.labelStyle = "right";
            },
        },
    },
    destroyed() {
        // 销毁enter事件
        this.enterKeyupDestroyed();
    },
};
</script>

<style>
#app {
    font-family: Avenir, Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    margin: 60px;
    display: flex;
    justify-content: center;
}

.top.search {
    margin-top: 50px;
}
</style>
