<template>
    <div class="card">
        <div class="cover">
            <img :src="imgSrc" @click="openVideo"/>
        </div>
        <div class="info">
            <div class="index">{{ index }}</div>
            <div class="title">{{ data.title | textLengthLimit }}</div>
            <div class="time">{{ data.time }}</div>
        </div>
    </div>
</template>

<script>
export default {
    name: "VideoUnit",
    props: ["data", "index"],
    data() {
        return {};
    },
    computed: {
        imgSrc() {
            return this.data.imgUrl
                ? this.data.imgUrl
                : "https://ys.mihoyo.com/main/_nuxt/img/37207c1.jpg";
        },
    },
    filters: {
        textLengthLimit(text) {
            if (text.length > 20) return text.slice(0, 20) + "...";
            else return text;
        },
    },
    methods: {
        openVideo() {
            window.open(this.data.videoUrl);
        },
    },
};
</script>

<style scoped>
.card {
    display: inline;
    width: 600px;
    height: 300px;
    overflow: hidden;
}
.cover {
    width: 600px;
    height: 250px;
    overflow: hidden;
    background-color: grey;
}
.info {
    width: 600px;
    height: 30px;
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
}
img {
    width: 600px;
    height: auto;
}
.index {
    margin-left: 6px;
}
</style>